import { Component, Input, Inject, PLATFORM_ID } from "@angular/core";
import { connectNumericSelector } from "instantsearch.js/es/connectors";
import { noop } from "lodash-es";

import { BaseWidget } from "../base-widget";
import { NgAisInstance } from "../instantsearch/instantsearch-instance";

export type NumericSelectorState = {
  currentRefinement?: string;
  options: {}[];
  refine: Function;
};


export class NgAisNumericSelector extends BaseWidget {
  // connector options
   public attributeName: string;
   public operator: "<" | "<=" | "=" | ">=" | ">" | "!=" = "=";
  
  public options: {
    value: number;
    label: string;
  }[];

  public state: NumericSelectorState = {
    currentRefinement: null,
    options: [],
    refine: noop
  };

  constructor(
     public platformId: Object,
    searchInstance: NgAisInstance
  ) {
    super(searchInstance, "NumericSelector");
  }

  public ngOnInit() {
    this.createWidget(connectNumericSelector, {
      attributeName: this.attributeName,
      operator: this.operator,
      options: this.options
    });
    super.ngOnInit();
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: "ng-ais-numeric-selector",
  template: `
    <div [class]="cx('')">
      <ng-ais-header [header]="header" [className]="cx('header')"></ng-ais-header>

      <div [class]="cx('body')">
        <select
          [class]="cx('select')"
          (change)="state.refine($event.target.value)"
        >
          <option
            [class]="cx('option')"
            *ngFor="let item of state.options"
            [value]="item.value"
            [selected]="item.value === state.currentRefinement"
          >
            {{item.label}}
          </option>
        </select>
      </div>

      <ng-ais-footer [footer]="footer" [className]="cx('footer')"></ng-ais-footer>
    </div>
  `
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: Object, decorators: [{ type: Inject, args: [PLATFORM_ID, ] }, ]},
{type: NgAisInstance, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'attributeName': [{ type: Input },],
'operator': [{ type: Input },],
'options': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
